import React, {useState} from 'react';
import {InputNumber} from "antd";
import {declOfNum} from "../../utils/declension";

const InputYear = ({onChange}) => {
    const [declYear, setDecl] = useState('');

    function onChangeYears(value) {
        setDecl(declOfNum(value, ['год', 'года', 'лет']));
        onChange(value);
    }
    return (
        <InputNumber min={1} max={30} addonAfter={declYear} onChange={onChangeYears}/>
    );
};

export default InputYear;